.footer {
  position: relative;
  width: 100%;
  padding-bottom: 80px;
  z-index: 1;
  background-color: #fff; }
  .footer .footer--email-container {
    position: absolute;
    top: 13.4%;
    right: 15vw; }
    .footer .footer--email-container > h3, .footer .footer--email-container div {
      display: inline-block; }
    .footer .footer--email-container h3 {
      color: #3726af;
      font-size: 1.0416vw;
      font-weight: 500;
      letter-spacing: -0.4px;
      margin-right: 2.0833vw; }
  .footer .footer--lower-container {
    width: 100%;
    border-top: 1px solid #cac6ec; }
    .footer .footer--lower-container > a {
      display: block;
      margin-top: 80px; }
      .footer .footer--lower-container > a div {
        display: inline-block;
        width: 80px;
        height: 80px;
        background: url("./Littleone Logo.svg") no-repeat 0/contain; }
    .footer .footer--lower-container > p {
      margin-top: 21px;
      font-size: 12px;
      color: #333; }
  .footer .footer--container {
    text-align: center;
    width: 100%; }
    .footer .footer--container > ul {
      width: 360px;
      padding-top: 56px;
      padding-bottom: 56px;
      margin-left: 15vw; }
      .footer .footer--container > ul li {
        float: left;
        width: 20%; }
        .footer .footer--container > ul li a {
          display: inline-block;
          width: 48px;
          height: 48px; }
        .footer .footer--container > ul li .facebook {
          background: url("./web-footer-btn-facebook.svg") no-repeat center/48px 48px; }
        .footer .footer--container > ul li .twitter {
          background: url("./web-footer-btn-twitter.svg") no-repeat center/48px 48px; }
        .footer .footer--container > ul li .instagram {
          background: url("./web-footer-btn-instagram.svg") no-repeat center/48px 48px; }
        .footer .footer--container > ul li .youtube {
          background: url("./web-footer-btn-youtube.svg") no-repeat center/48px 48px; }
        .footer .footer--container > ul li .email {
          background: url("./web-group-btn-email.svg") no-repeat center/48px 48px; }

@media only screen and (max-width: 1440px) {
  .footer .footer--email-container {
    right: 8vw; }
  .footer .footer--container > ul {
    margin-left: 8vw; } }

@media only screen and (max-width: 1024px) {
  .footer .footer--email-container {
    right: initial;
    position: relative;
    display: block;
    top: initial; }
    .footer .footer--email-container > h3, .footer .footer--email-container div {
      display: block;
      margin: 0 auto; }
    .footer .footer--email-container h3 {
      font-size: 16px; }
    .footer .footer--email-container div {
      margin: 25px auto; }
  .footer .footer--container > ul {
    margin-left: 0;
    display: inline-block; } }

@media only screen and (max-width: 640px) and (max-width: 1024px) {
  .footer .footer--email-container h3 {
    font-size: 14px; }
  .footer .footer--container > ul {
    width: 90%; }
    .footer .footer--container > ul li .facebook {
      background-size: 36px 36px; }
    .footer .footer--container > ul li .twitter {
      background-size: 36px 36px; }
    .footer .footer--container > ul li .instagram {
      background-size: 36px 36px; }
    .footer .footer--container > ul li .youtube {
      background-size: 36px 36px; }
    .footer .footer--container > ul li .email {
      background-size: 36px 36px; } }
