.header-section {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #fff;
  margin: 0 auto;
  -webkit-transition: all 0.4s ease-out;
  -moz-transition: all 0.4s ease-out;
  -ms-transition: all 0.4s ease-out;
  -o-transition: all 0.4s ease-out;
  transition: all 0.4s ease-out;
  text-align: center;
  z-index: -2;
  border-bottom: 1px solid #cac6ec;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  max-height: 55px; }
  .header-section .__link-logo {
    display: inline-block;
    padding: 10px 0; }
    .header-section .__link-logo img {
      width: 124px; }
  .header-section--container {
    position: absolute;
    top: 0;
    right: 0;
    padding: 24px 0;
    display: inline-block; }
  .header-section .__sns-link {
    display: inline-block;
    width: 42px;
    height: 42px;
    overflow: hidden;
    text-indent: -9999px;
    margin-left: 14px; }

@media only screen and (max-width: 768px) {
  .header-section {
    position: relative;
    top: initial; }
    .header-section .__link-logo img {
      width: 96px;
      height: auto; }
    .header-section--container {
      padding: 19px 0; }
    .header-section .__sns-link {
      width: 30px;
      height: 30px; } }

@media only screen and (max-width: 640px) {
  .header-section .__link-logo {
    padding: 5px 0; }
  .header-section .__select-country.active .country-box {
    top: -4px;
    padding: 0 6px; } }

@media only screen and (max-width: 320px) {
  .header-section .__link-logo img {
    width: 100px; } }
