.slider-section-upper {
  width: 100%;
  overflow: hidden;
  margin-top: 60.25vw;
  -webkit-box-shadow: 0px -10px 52px -8px rgba(0, 0, 0, 0.4);
  -moz-box-shadow: 0px -10px 52px -8px rgba(0, 0, 0, 0.4);
  box-shadow: 0px -10px 52px -8px rgba(0, 0, 0, 0.4); }
